<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<h1>Settings</h1><hr>
				<form @submit.prevent="updateSettings();">
					<fieldset>	
						<fieldset class="form-group">
							Name
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.username"
							@click="contactStashAsia"
							placeholder="Your username"
							disabled
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Email
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.email"
							placeholder="Email"
							@click="contactStashAsia"
							disabled
							/>
						</fieldset><br>
						<button class="btn btn-lg btn-primary pull-xs-right">
							Update Profile
						</button>	
					</fieldset>
				</form>
				<br>
        <br>
        <h5>Related:</h5>
        <router-link :to="{ name: 'passwordUpdate' }">
          <h3><i class="md-36 material-icons main">security</i> Password update</h3>
        </router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from "vuex";

	export default {
	  name: "viewProfileUpdates",
	  computed: {
		...mapGetters(["currentUser"])
	  },
	  methods: {
			contactStashAsia() {
				// alert("")
					
				this.$swal.fire({
					type: 'info',
					text: 'Please contact our hello@stash.asia',
					showConfirmButton: false,
					showCloseButton: true,
				})
			},

			updateSettings() {
				this.$store.dispatch(UPDATE_USER, this.currentUser).then(() => {
					// #todo, nice toast and no redirect
					this.$router.push({ name: "home" });
				});
			},
			
	  }
	};
</script>



