<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Password Update</h1>
        <hr>
				<form @submit.prevent="updateSettings();">
					<fieldset>
						<fieldset class="form-group">
							Current passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentUser.password"
							placeholder="Password"
							/>
						</fieldset><br>
						<fieldset class="form-group">
							New passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentUser.password"
							placeholder="Password"
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Repeat new passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentUser.password"
							placeholder="Password"
							/>
						</fieldset><br>
						<button class="btn btn-lg btn-primary pull-xs-right">
							Update Password
						</button>	
					</fieldset>
				</form>
        <br>
        <br>
        <h5>Related:</h5>
        <router-link :to="{ name: 'profileUpdate' }">
          <h3><i class="md-36 material-icons main">person</i> Profile update</h3>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
  
export default {
  name: 'viewPasswordUpdates',
  computed: {
  ...mapGetters(["currentUser"])
  },
}
</script>

<style>

</style>



